export class Utilitarios {

    // suma las columnas especificadas de un conjunto de datos
    sumarColumdaData(data: any[], columnas:string[]): any {
        let rpt: any = [];
        let suma: number = 0;        
        columnas.map(x => {
            data.map(z => {               
                return suma += z[x];
            });
            rpt.push({'header': x, 'suma': suma});
        })
        return rpt;
    }

}